import React, { useState } from 'react';
import classes from './Admin.module.css'
import Select from '../../UI/Select/Select';
import ButtonContainer from '../../UI/ButtonContainer/ButtonContainer';
import Container from '../../UI/Container/Container';
import GreenButton from '../../UI/GreenButton/GreenButton';
import RedButton from '../../UI/RedButton/RedButton';
import Input from '../../UI/Input/Input';
import Table from '../../UI/Table/Table/Table';

const curs = [1, 2, 3]
const tables = ["table1", "table2", "table3"];


const Admin = () => {
    const [data, setData] = useState([
        ["", "", "", ""],
        ["a", "1", "2", "3"],
        ["b", "4", "5", "6"]
    ]);

    return (
        <div className={classes.Admin}>
            <div className={classes.TitleContainer}>
                <h1 className={classes.Title}>  </h1>
            </div>
            <Select text={`    `} title={"  "} options={curs} currentIndex={0} className={classes.CurSelector} />
            <Select text={`   `} title={" "} options={tables} currentIndex={0} className={classes.TableSelector} />
            <ButtonContainer className={classes.ResetTable} text={"  "}> </ButtonContainer>
            <ButtonContainer className={classes.NewTable} text={"     "}> </ButtonContainer>
            <ButtonContainer className={classes.DeleteTable} text={"  "}> </ButtonContainer>
            <RedButton className={classes.Deny}> </RedButton>
            <GreenButton className={classes.Apply}> </GreenButton>
            <Container className={classes.TableContainer}>
                <Input className={classes.TableName} placeholder={"  "} text={" "} />
                <Table data={data} setData={setData} className={classes.Table}/>
                <ButtonContainer className={classes.AddRow} text={"    "}> </ButtonContainer>
                <ButtonContainer className={classes.DeleteRow} text={"    "}> </ButtonContainer>
                <ButtonContainer className={classes.AddColumn} text={"    "}> </ButtonContainer>
                <ButtonContainer className={classes.DeleteColumn} text={"    "}> </ButtonContainer>
            </Container>
        </div>
    );
};

export default Admin;